import React, { useState, useEffect } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import {
 Content,
 Header,
 Page,
 pageTheme,
 ContentHeader,
} from '@backstage/core';
import { makeStyles, createStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import FormControl from '@material-ui/core/FormControl';
import { Alert } from '@material-ui/lab';
import { DefaultApi } from '../../api/apis';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';




const useStyles = makeStyles((theme) =>
 createStyles({
   root: {
     display: 'flex',
     flexWrap: 'wrap',
     justifyContent: 'center',
   },
   margin: {
     margin: theme.spacing(3),
   },
   withoutLabel: {
     marginTop: theme.spacing(3),
   },
   textField: {
     width: '25ch',
   },
 }),
);

const username={ givenuser: '. '}
export default function Create() {
 const classes = useStyles();
 const profile = { givenName: '' };
 const api = new DefaultApi();
 
 //const [user, setUser] = useState(initialUserState);
 const [users, setUsers] = useState([]);
 const [patients, setPatients] = useState([]);
 const [statuss, setStatuss] = useState([]);
 const [status, setStatus] = useState(false);
 const [alert, setAlert] = useState(true);
 const [loading, setLoading] = useState(true);

 //const [treatment, setTreatment] = useState(String);
 const [statustrack, setStatustrack] = useState(Number);
 const [symptom, setSymptom] = useState(String);
 const [userid, setUserid] = useState(Number);
 const [patientid, setPatientid] = useState(Number);
 const [addtime, setAddtime] = useState(String);
 
 useEffect(() => {
   const getUsers = async () => {
     const u = await api.listUser({ limit: 10, offset: 0 });
     setLoading(false);
     setUsers(u);
   };
   getUsers();

   const getPatients = async () => {
    const p = await api.listPatient({ limit: 10, offset: 0 });
    setLoading(false);
    setPatients(p);
  };
  getPatients();

  const getStatuss = async () => {
    const st = await api.listStatustracking({ limit: 10, offset: 0 });
    setLoading(false);
    setStatuss(st);
  };
  getStatuss();

 }, [loading]);
 
 const handlesymptomChange = (event) => {
  setSymptom(event.target.value );
 };

 const handletimeChange = (event) => {
  setAddtime(event.target.value );
 };

 const CreateTreatment = async () =>{
  const treatment = {
    userid    : userid,
	  patientid : patientid,
	  status    : statustrack,
	  symptom   : symptom,
    added     : addtime + "T00:00:00Z"
  }

   const res = await api.createTreatment({ treatment : treatment });
   setStatus(true);
   if (res.id != ''){
     setAlert(true);
   } else {
     setAlert(false);
   }
   const timer = setTimeout(() => {
     setStatus(false);
   }, 1000);
 };


 const doctor_id_handleChange = (event) => {
  setUserid(event.target.value );
 };

const patient_id_handleChange = (event) => {
  setPatientid(event.target.value );
 };

 const status_patienthandleChange = (event) => {
  setStatustrack(event.target.value );
 };
 
 return (
   React.createElement(Page, { theme: pageTheme.home,}
     , React.createElement(Header, {
       title: `Welcome ${profile.givenName || ''}`,
       subtitle :  `${username.givenuser}`,}
     )
     , React.createElement(Content, null
       , React.createElement(ContentHeader, { title: "",}
       , React.createElement(Button, { variant: "outlined", color: "secondary", href: "#outlined-buttons",}, "LogOut"

        )
         , status ? (
           React.createElement('div', null
             , alert ? (
               React.createElement(Alert, { severity: "success",}, "This is a success alert  check it out!"

               )
             ) : (
               React.createElement(Alert, { severity: "warning", style: { marginTop: 20 },}, "This is a warning alert  check it out!"

               )
             )
           )
         ) : null
       )

       , React.createElement('div', { className: classes.root,}
         , React.createElement('form', { noValidate: true, autoComplete: "off",}
         , React.createElement('div', null
, React.createElement(FormControl, { 
             className: classes.margin,
             variant: "outlined",}
           
            , React.createElement(InputLabel, { id: "doctor_id-label",}, "Doctor_ID")
            , React.createElement(Select, {
              labelId: "doctor_id-label",
              label: "Doctor",
              id: "doctor_id",
              value: userid,
              onChange: doctor_id_handleChange,
              style :  {width: 600},}
            
              , users.map((item)=>
              React.createElement(MenuItem, { value: item.id,}, item.doctorName))
            )
 )
 )
, React.createElement('div', null
, React.createElement(FormControl, { 
             className: classes.margin,
             variant: "outlined",}
           
            , React.createElement(InputLabel, { id: "patient_id-label",}, "Patient_ID")
            , React.createElement(Select, {
              labelId: "patient_id-label",
              label: "Patient",
              id: "patient_id",
              value: patientid,
              onChange: patient_id_handleChange,
              style :  {width: 600},}
            
              , patients.map((item)=>
              React.createElement(MenuItem, { value: item.id,}, item.patientName))
            )
 )
 )

           , React.createElement(FormControl, {
            // fullWidth
             className: classes.margin,
             variant: "outlined",}
           
             , React.createElement(TextField, {
               id: "symptom",
               label: "Symptom",
               variant: "outlined",
               type: "string",
               size: "medium",
               value: symptom,
               onChange: handlesymptomChange,
               style :  {width: 600},}
             )
           )

, React.createElement('div', null
, React.createElement(FormControl, { 
             className: classes.margin,
             variant: "outlined",}
           
            , React.createElement(InputLabel, { id: "status-label",}, "Status")
            , React.createElement(Select, {
              labelId: "status-label",
              label: "Stauts",
              id: "status_patient",
              value: statustrack,
              onChange: status_patienthandleChange,
              style :  {width: 600},}
            
              , statuss.map((item)=>
              React.createElement(MenuItem, { value: item.id,}, item.status))
            )
          )
 )

 , React.createElement(FormControl, { className: classes.margin,} 
      , React.createElement(TextField, {
        id: "Appoint_date",
        label: "Appoint_date",
        type: "date",
        value: addtime,
        onChange: handletimeChange,
        //defaultValue="2020-05-24"
        className: classes.textField,
        InputLabelProps: {
          shrink: true,
        },}
        
      )
    )

           , React.createElement('div', { className: classes.margin,}
             , React.createElement(Button, {
               onClick: () => {
                 CreateTreatment();
               },
               variant: "contained",
               color: "primary",}
             , "Submit"

             )
             , React.createElement(Button, {
               style: { marginLeft: 20 },
               component: RouterLink,
               to: "/",
               variant: "contained",}
             , "Back"

             )
           )
         )
       )
     )
   )
 );
}
